body {
  background: #070709;
  color: #d9d9d9;
  text-align: center;
  font-family: "Nunito", sans-serif;
  letter-spacing: 0.1em;
  box-sizing: border-box;
  margin: 0;
  min-width: 296px;
}

h1 {
  font-size: 18px;
  margin: 40px;
  letter-spacing: 0.5em;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

footer {
  padding-top: 10vh;
}

footer i {
  font-size: 1.3em;
  padding: 20px;
  color: white;
}

footer i:hover {
  color: #bf4aa8;
}

.App {
  text-align: center;
}

/*** Drum machine block ***/
#display {
  align-content: center;
  text-align: center;
}

.switches {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

#drum-machine {
  max-width: 500px;
  min-width: 336px;
  border-radius: 8px;
  padding: 10px;
  margin: auto;
  background: #51d0de;
}

#screenOff {
  display: inline-block;
  width: 60%;
  height: 40px;
  margin-top: 14px;
  text-align: center;
  line-height: 38px;
  background: #bf4aa8;
  box-shadow: inset 0 0 45px #262626;
  border-radius: 8px;
}

#screenOn {
  display: inline-block;
  width: 60%;
  height: 40px;
  margin-top: 14px;
  text-align: center;
  line-height: 38px;
  color: #bf4aa8;
  background: #d9d9d9;
  border: 1px solid #bf4aa8;
  box-shadow: 0 0 6px 2px #bf4aa8;
  border-radius: 8px;
}

#power {
  display: flex;
  background: #bf4aa8;
  border: 1px solid #58214d;
  box-shadow: 0 0 6px 2px #193b3f;
  border-radius: 8px;
  justify-content: space-between;
  width: 70px;
  height: 40px;
  padding: 10px 10px;
  cursor: pointer;
  color: #d9d9d9;
}

.lightOn {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #29eaff;
  border: 1px solid #1b8377;
  box-shadow: 0 0 10px 1px #2ffde2;
}

.lightOff {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #848788;
  border: 1px solid #adadad;
  box-shadow: inset 0 0 5px #262626;
}

#mode {
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#mode-switch {
  margin: 10px;
  width: 130px;
  height: 15px;
  background: grey;
  cursor: pointer;
  box-shadow: 0 0 6px 2px #193b3f;
  border-radius: 8px;
}

#mode1 {
  height: 100%;
  width: 50%;
  background: #bf4aa8;
  float: left;
  border-radius: 8px;
}

#mode2 {
  height: 100%;
  width: 50%;
  background: #bf4aa8;
  float: right;
  border-radius: 8px;
}

#drums {
  max-width: 380px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 10px;
  margin: auto;
}

.drum-pad {
  width: 100px;
  height: 100px;
  padding-block: 2em;
  border-radius: 2px;
  background: #d9d9d9;
  border: 1px solid #bf4aa8;
  box-shadow: 0 0 1px 2px #b179a5;
  cursor: pointer;
  text-align: center;
  margin: 5px auto;
}

.drum-pad p {
  position: relative;
  top: 35px;
  color: #2ffde2;
  font-size: 1.6em;
  text-shadow: 0 0 10px #131212;
}